body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body{
  background: black;
}
.app {
  text-align: center;
  color: white;
}

.board{
  margin-top: 10px;
}

.score{
  color: white;
  position: absolute;
  bottom:0;
  left: 80px;
  /* background: blue; */
}
.canvas{
  margin:auto;
  position: relative;
  /* background: green; */
}
.paused{
  position: absolute;
  left:0px;
  right:0px;
  margin: auto;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  color: white;
  -webkit-text-stroke: 2px black;
}
.start{
  color: white;
  position: absolute;
  top: 0px;
  right: 80px;
  /* background: red; */
}
.gameStart button{
  font-size: 16px;
  height: 50px;
  width: 100px;
}
.points{
  margin-top: -5px;
}
